import React, { Component } from "react";
import PropTypes from "prop-types"
import s from "./Styles.module.css"
import { printDiffLines } from "jest-diff/build/printDiffs";
class ContactList extends Component{
    
    render() {
        return (
        <>
            <div>
                <ul>
        {this.props.contactitems.map(contactitem => (
        <li id={contactitem.id} className={s.contact}>
        <span> Name:{contactitem.name} Phone:{contactitem.phone}</span><button
        type="button"  onClick={() => this.props.onDeleteContact(contactitem.id)}>Удалить</button>
        </li>
      ))}
                </ul>
            </div>
        </>
    )  }
}
export default ContactList;

ContactList.propTypes = {
    onDeleteContact: PropTypes.func.isRequired,
    contactitems: PropTypes.array.isRequired,
}