import React, { Component } from "react";
import shortid from "shortid";
import PropTypes from "prop-types";
import s from "./Styles.module.css"
class Phonebook extends Component {
    state = {
      name: '',
  phone:''
    }
  handleChange = event => {
    const { name, value } = event.currentTarget;
    this.setState({ [name]: value });
  }
  handleSubmit = e => {
    const { name, phone } = this.state;
    const id = shortid.generate();
    const data = { id, name, phone };
    const notUniqueName = this.props.contacts.some(
      (item) => item.name === this.state.name
    );
    const notUniqueNumber = this.props.contacts.some(
      (item) => item.number === this.state.phone
    );
    e.preventDefault();
      if (notUniqueName) {
      return alert(this.state.name + " уже добавлен(а) в список контактов");
    } else if (notUniqueNumber) {
      return alert("Уже добавлен контакт с номером: " + this.state.phone);
    } else if (this.state.name === "" || this.state.phone === "") {
      return alert("Пожалуйста, введите Имя и номер.");
    } else {
        this.props.onSubmit(data);
    this.reset();
    }
  }
  reset = () => {
    this.setState({  name: '',
  phone:''})
  }
  render() {
  return (
  <>
  <form onSubmit={this.handleSubmit} className={s.phonebook_form}>
        <label>Имя<input
          type="text"
          className={s.form_input}
  name="name"
  pattern="^[a-zA-Zа-яА-Я]+(([' -][a-zA-Zа-яА-Я ])?[a-zA-Zа-яА-Я]*)*$"
  value={this.state.name} onChange={this.handleChange}
  title="Имя может состоять только из букв, апострофа, тире и пробелов. Например Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan и т. п."
  required/>Phone<input type="text" className={s.form_input}
  name="phone"
  value={this.state.phone} onChange={this.handleChange}
  title="Имя может состоять только из букв, апострофа, тире и пробелов. Например Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan и т. п."
  required
/>
</label>
<button type="submit" className={s.form_btn}>Add contact</button>
           </form>
      </>
    );
  }
 }
export default Phonebook;

Phonebook.propTypes = {
  onSubmit: PropTypes.func.isRequired,
  contacts: PropTypes.array.isRequired,
}